import React from "react";
import { Select } from "antd";
import { useQuery } from "react-query3";
import { Framework } from "../../types";

export default function DashboardTemplateSelect(props) {
    const { data, isLoading } = useQuery<Framework[]>("/dashboardframework");
    return (
        <Select {...props} loading={isLoading} defaultValue="Blank.ps1">
            {data?.find(m => m.latest)?.templates?.map((template) => (
                <Select.Option key={template} value={template}>{template}</Select.Option>
            ))}
        </Select>
    );
}
